<template>
  <div>
    <h2>UI 개발팀 법인카드 사용내역</h2>

    <div class="get-list">
      <button class="get-list-button" @click="getList">사용내역 가져오기</button>
    </div>
    <div class="table-scroll">
      <table>
        <tr>
          <th></th>
          <th>내역등록일시</th>
          <th>사용일시</th>
          <th>사용 내역 분류</th>
          <th>사용금액</th>
          <th>잔액</th>
          <th>비고</th>
        </tr>
        <tr v-for="(item, index) in historyList" :key="index">
          <td><input type="checkbox" v-bind:value="index" v-model="checkedValue"></td>
          <td>{{item.date}}</td>
          <td>{{item.usedDate || ''}}</td>
          <td>{{getCategoryName(item.category)}}</td>
          <td>-{{item.amount | numberWithCommas}}</td>
          <td>{{item.balance | numberWithCommas}}</td>
          <td>{{item.memo}}</td>
        </tr>
      </table>
    </div>

    <div class="edit-area">
      <button class="edit btn" @click="editUsage">수정</button>
      <button class="delete btn" @click="deleteUsage">삭제</button>
    </div>

    <div class="input-area">
      <!-- <input v-model="usedDate" placeholder="사용일시(필수)"> -->
      <Datepicker
        class="datepicker"
        v-model="usedDate"
        :format="customFormatter"
        placeholder="사용일시(필수)"
      />
      <select v-model="category">
          <option value="">분류선택(필수)</option>
          <option value="0">식사</option>
          <option value="1">간식</option>
          <option value="2">접대</option>
          <option value="3">비품</option>
          <option value="4">회식</option>
          <option value="5">기타</option>
      </select>

      <input v-model="amount" placeholder="사용금액(필수)" @keyup="addCommas" style="ime-mode:disabled">
      <input v-model="memo" placeholder="비고">

      <button class="registration" @click="addUsage">등록</button>
    </div>

  </div>
</template>

<script>

import common from '@/mixins/common';
import dayjs from 'dayjs';
import Datepicker from 'vuejs-datepicker';

export default {
  name: 'CorpCard',
  components: {
    Datepicker
  },
  props: {
    msg: String
  },
  mixins: [common],
  data() {
    return {
      usedDate: null,
      category: '',
      amount: null,
      memo: null,
      checkedValue: []
    }
  },
  filters: {
    numberWithCommas: n => {
      return n.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
    }
  },
  created () {
    // this.initialize();
  },
  methods: {
    getCategoryName(item) {
      let categoryName = '';
      if (item === '0') {
        categoryName = '식사';
      } else if (item === '1') {
        categoryName = '간식';
      } else if (item === '2') {
        categoryName = '접대';
      } else if (item === '3') {
        categoryName = '비품';
      } else if (item === '4') {
        categoryName = '회식';
      } else if (item === '5') {
        categoryName = '기타';
      }

      return categoryName;
    },
    addCommas(event) {
      return event.key.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
    getList() {
      this.getHistory();
    },
    addUsage() {
      if (!this.usedDate) {
        alert('사용일시를 입력해주세요.');
        return;
      } else if (!this.category) {
        alert('분류를 선택해주세요.');
        return;
      } else if (!this.amount) {
        alert('사용금액을 입력해주세요.');
        return;
      }

      const useObejct = {
        usedDate: dayjs(this.usedDate).format('YYYY-MM-DD'),
        category: this.category,
        amount: this.amount,
        memo: this.memo || '',
        date: dayjs().format('YYYY-MM-DD HH:mm:ss'),
        timeInMs: Date.now() // time stamp
      }

      this.registration(useObejct);
    },
    editUsage() {
      // 수정
    },
    deleteUsage() {
      // 삭제
    },
    customFormatter(value) {
      return dayjs(value).format('YYYY-MM-DD');
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
select {
  width: 120px;
  height: 30px;
  margin-right: 10px;
}

table {
  font-family: arial, sans-serif;
  border-collapse: collapse;
  width: 100%;
}

td, th {
  border: 1px solid #dddddd;
  text-align: center;
  padding: 8px;
}

tr:nth-child(even) {
  background-color: #dddddd;
}

.get-list {
  margin: 20px;
}

.edit-area {
  width: 150px;
  margin-top: 20px;
}

.btn {
  width: 50px;
  height: 50px;
  margin: 10px;
  font-size: 1em;
  text-align: center;
  font-weight: bold;
}

.input-area {
  margin: 20px;
}

.get-list-button{
  width: 200px;
  height: 50px;
  font-size: 1em;
  text-align: center;
  font-weight: bold;
}

.table-scroll {
  width:100%;
  height:600px;
  overflow:auto;
}

.registration {
  margin-left: 10px;
}
</style>
